<script>
	import ethers from 'ethers';

	const projectId = process.env.INFURA_PROJECT_ID;

	const network = "ropsten";

    const provider = ethers.getDefaultProvider(network, {
        infura: projectId
	});
	
	provider.getBlockNumber()
	.then((num)=> {
		console.log('num: ', num)
	})
	.catch((err)=> {
		console.log('err: ', err)
	})
</script>

<h1>Hello</h1>
